import {Grid} from "@mui/material";
import MyAppBar from "../MyAppBar";
import MyButton from "./MyButton";
import React from "react";
import KakaoMap from "./KakaoMap";
import KeywordSearchLabel from "./KeywordSearchLabel";

function App() {
    return (
        <div>
            <MyAppBar name={"국내 여행"}/>
            <Grid
                container
                alignItems="center"
                style={{
                    background: "rgb(207, 200, 250)",
                    height: "92.5%",
                }}
            >
                <Grid item xs={12} className="top_container">
                    <MyButton/>
                </Grid>
                <Grid item xs={12} style={{margin:'0 1rem 1rem 1rem' }}>
                    <KeywordSearchLabel />
                </Grid>

                <Grid item xs={12}>
                    <KakaoMap/>
                </Grid>
            </Grid>
        </div>
    );
}

export default App;
