import {useRef} from "react";
import {Button} from "@mui/material";
import {useDomesticStore} from "./useDomesticStore";
import axios from "axios";
import {REQUEST_URL, serviceKey, transformResponse} from "../../config/config";

export default function KeywordSearchLabel(props) {
    const {setCenter, setDataList} = useDomesticStore();
    const onSearchClick = (keyword) => {
        const ps = new window.kakao.maps.services.Places();

        // 키워드로 장소를 검색합니다
        ps.keywordSearch(keyword, (data, status, pagination) => {
            if (status === window.kakao.maps.services.Status.OK) {
                setCenter({
                    lat: data[0]?.y,
                    lng: data[0]?.x,
                })
                axios.get(REQUEST_URL.DOMESTICAPI, {
                    params: {
                        serviceKey: serviceKey,
                        numOfRows: 10000,
                        pageNo: 1,
                        _type: "json",
                        MobileOS: "ETC",
                        MobileApp: "TravelSupporter",
                        listYN: "Y",
                        arrange: "R",
                        mapX: data[0]?.x,
                        mapY: data[0]?.y,
                        radius: 10000,
                    },
                }).then((response) => {
                    const list = transformResponse(response.data.response);
                    setDataList(list);
                }).catch((error) => {
                    console.log(error);
                });

            }
        });
    }

    const target = useRef();
    const handleOnChange = (e) => {
        target.current.value = e.target.value;
    }

    const onKeyDown = (e) => {
        if (e.key === "Enter") {
            onSearchClick(target.current.value);
        }
    }

    return (
        <div className="group">
            <svg className="icon" aria-hidden="true" viewBox="0 0 24 24">
                <g>
                    <path
                        d="M21.53 20.47l-3.66-3.66C19.195 15.24 20 13.214 20 11c0-4.97-4.03-9-9-9s-9 4.03-9 9 4.03 9 9 9c2.215 0 4.24-.804 5.808-2.13l3.66 3.66c.147.146.34.22.53.22s.385-.073.53-.22c.295-.293.295-.767.002-1.06zM3.5 11c0-4.135 3.365-7.5 7.5-7.5s7.5 3.365 7.5 7.5-3.365 7.5-7.5 7.5-7.5-3.365-7.5-7.5z"/>
                </g>
            </svg>
            <input ref={target} placeholder={props.placeholder} onKeyDown={onKeyDown} onChange={handleOnChange}
                   type="search"
                   className="input"/>
            <Button onClick={() => {
                onSearchClick(target.current.value)
            }} sx={{
                position: "absolute",
                right: "10px", /* 조정하여 input 내부에 위치하게 함 */
                height: "36px", /* input 높이에 맞춤 */
                background: "#c5e5fa",
                color: "#0d0c22"
            }}>검색</Button>
        </div>);
}