import "./todoList.css";
import {
    Box,
    Button,
    Checkbox,
    Container,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    FormControlLabel,
    Grid,
    IconButton,
    List,
    ListItem,
    TextField,
    Typography
} from "@mui/material";
import {useState} from "react";
import axios from "axios";
import {useUserStore} from "../Authentication/useUserStore";
import {Link, useNavigate} from "react-router-dom";
import {REQUEST_URL} from "../../config/config";
import ExitToAppIcon from '@mui/icons-material/ExitToApp';

function TodoList() {
    const {todoList, setTodoList, isAuthenticated, userDTO, logout} = useUserStore();

    const [addTask, setAddTask] = useState(false);
    const [showDetail, setShowDetail] = useState(false);
    const [modify, setModify] = useState(false);
    const [detailTask, setDetailTask] = useState("");
    const [todoDTO, setTodoDTO] = useState({
        id: null,
        title: "",
        content: "",
        userId: "",
        isCompleted: false,
        createdDate: null,
        dueDate: null,
    });

    const addRequest = async (todoDTO) => {
        await axios.post(REQUEST_URL.DEFAULT_TODO, todoDTO).then((res) => {
            setTodoList(res.data);
        });
    };

    const modifyRequest = async (todoDTO) => {
        await axios.patch(REQUEST_URL.DEFAULT_TODO, todoDTO)
            .then((res) => {
                setTodoList(res.data);
            });
    };

    const deleteRequest = async (todoDTO) => {
        await axios
            .delete(REQUEST_URL.TODO + todoDTO.id, todoDTO)
            .then((res) => {
                setTodoList(res.data);
            });
    };

    const changeRequest = async (todoDTO) => {
        await axios
            .patch(REQUEST_URL.TODO + todoDTO.id, todoDTO)
            .then((res) => {
                setTodoList(res.data);
            });
    };

    const navigate = useNavigate();
    const addTaskOpen = () => {
        isAuthenticated
            ? setAddTask(true)
            : (() => {
                alert("로그인이 필요한 서비스 입니다.");
                navigate("/auth");
            })();
    };

    const addTaskClose = () => {
        setAddTask(false);
    };

    const showDetailOpen = () => {
        setShowDetail(true);
    };

    const showDetailClose = () => {
        setShowDetail(false);
    };

    const showModify = () => {
        setModify(true);
    };

    const showModifyClose = () => {
        setModify(false);
        setShowDetail(false);
    };

    const addTasks = () => {
        if (todoDTO.title === "") {
            alert("필수 항목을 입력해주세요");
        } else {
            addRequest({...todoDTO, userId: userDTO.id});
            setTodoDTO({
                id: null,
                title: "",
                content: "",
                userId: "",
                isCompleted: false,
                createdDate: null,
                dueDate: null,
            });
            addTaskClose();
        }
    };

    const detail = (event, task) => {
        event.preventDefault();
        setDetailTask(task);
        showDetailOpen();
    };

    const isCheck = (index, event) => {
        todoList.map(
            (task, idx) =>
                idx === index &&
                changeRequest({...task, isCompleted: event.target.checked})
        );
    };

    const closeDetail = () => {
        setDetailTask("");
        setShowDetail(false);
    };

    const deleteTask = () => {
        deleteRequest(detailTask);
        setShowDetail(false);
    };

    const applyModify = () => {
        modifyRequest(detailTask);
        showModifyClose();
    };

    const loginPage = () => {
        console.log("dd");
    };

    return (
        <Container id="outLine" maxWidth="xs" style={{height: "95vh"}}>
            <Grid id="topBox" container alignItems="center">
                <Grid id="topText" item xs={12}>
                    <h1>To Do List</h1>
                </Grid>

                <Grid item xs={12}>
                    <Link to={!isAuthenticated ? "/auth" : null}>
                        <img
                            onClick={loginPage}
                            src={"/user.png"}
                            width="70"
                            height="70"
                            style={{marginTop: "-10px"}}
                            alt={""}
                        />
                    </Link>
                </Grid>
                <Grid item xs={12} style={{marginLeft: `${isAuthenticated ? "1rem" : "0"}`, marginBottom: "3vh"}}>
                    <Typography variant="h6" style={{fontWeight: "bold", fontFamily:'Mapo'}}>
                        {isAuthenticated ? (
                            <>
                                {userDTO.name}님 환영합니다
                                <IconButton style={{color: 'black'}} onClick={logout}>
                                    <ExitToAppIcon/>
                                </IconButton>
                            </>
                        ) : (
                            "Guest"
                        )}
                    </Typography>
                </Grid>
            </Grid>

            <Grid id="middleBox" container>
                <Grid item xs={4} id="middleBoxs">
                    {todoList?.filter((todo) => !todo.isCompleted).length} <br/> To Do
                </Grid>

                <Grid item xs={4} id="middleBoxs">
                    {todoList.length} <br/> ALL
                </Grid>
            </Grid>

            <Box id="middleLine"></Box>

            <Grid id="bottomBox" direction="column" container>
                <Grid item id="today_Task" xs={12}>
                    My Todo List
                </Grid>
                <List id="taskList" sx={{overflowY: "auto"}}>
                    {todoList.map((task, index) => (
                        <ListItem key={index}>
                            <FormControlLabel
                                id="task"
                                style={{justifyContent: 'left', width: '95%', paddingLeft: '5px', paddingRight: '5px'}}
                                control={
                                    <Checkbox
                                        defaultChecked={task.isCompleted}
                                        onChange={(check) => {
                                            isCheck(index, check);
                                        }}
                                    />
                                }
                                label={
                                    <Typography
                                        onClick={(event) => {
                                            detail(event, task);
                                        }}
                                        textAlign="left"
                                        textOverflow='ellipsis'
                                        overflow='hidden'
                                        whiteSpace='no-wrap'
                                        style={{
                                            fontFamily: 'Mapo',
                                            textDecoration: task.isCompleted
                                                ? "line-through"
                                                : "none",
                                        }}
                                    >
                                        {task.title}
                                    </Typography>
                                }
                            />
                        </ListItem>
                    ))}
                </List>
                <Button id="addTaskBtn" onClick={addTaskOpen}>
                    +
                </Button>
                <Dialog open={addTask} onClose={addTaskClose}>
                    <DialogTitle>할 일 추가</DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            required="true"
                            margin="dense"
                            label="To Do"
                            type="text"
                            fullWidth
                            variant="standard"
                            onChange={(e) => {
                                const newTitle = {
                                    ...todoDTO,
                                    title: e.target.value,
                                };
                                setTodoDTO(newTitle);
                            }}
                        />
                        <TextField
                            margin="dense"
                            type="date"
                            fullWidth
                            variant="standard"
                            value={todoDTO.dueDate}
                            onChange={(e) => {
                                const newDate = {
                                    ...todoDTO,
                                    dueDate: e.target.value,
                                };
                                setTodoDTO(newDate);
                            }}
                        />
                        <TextField
                            margin="dense"
                            label="Memo"
                            type="text"
                            fullWidth
                            variant="standard"
                            value={todoDTO.content}
                            onChange={(e) => {
                                const newMemo = {
                                    ...todoDTO,
                                    content: e.target.value,
                                };
                                setTodoDTO(newMemo);
                            }}
                        />
                        *는 필수 입력 사항 입니다.
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={addTaskClose}>Cancel</Button>
                        <Button onClick={addTasks}>Add</Button>
                    </DialogActions>
                </Dialog>

                <Dialog open={showDetail} onClose={showDetailClose}>
                    <DialogTitle> 상세 보기</DialogTitle>
                    <DialogContent>
                        <Typography style={{width: "100%"}}>
                            To Do : {detailTask.title}
                        </Typography>
                        <Typography style={{width: "100%"}}>
                            Memo : {detailTask.content != null ? detailTask.content : ""}
                        </Typography>
                        <Typography style={{width: "100%"}}>
                            DueDate : {detailTask.dueDate != null ? detailTask.dueDate : ""}
                        </Typography>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={closeDetail}>Close</Button>
                        <Button onClick={deleteTask}>Delete</Button>
                        <Button onClick={showModify}>modify</Button>
                    </DialogActions>
                </Dialog>

                <Dialog open={modify} onClose={showModifyClose}>
                    <DialogTitle>To Do 수정</DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            margin="dense"
                            label="To Do"
                            type="text"
                            fullWidth
                            variant="standard"
                            value={detailTask.title}
                            onChange={(e) => {
                                const newTitle = {
                                    ...detailTask,
                                    title: e.target.value,
                                };
                                setDetailTask(newTitle);
                            }}
                        />
                        <TextField
                            margin="dense"
                            type="date"
                            fullWidth
                            variant="standard"
                            value={detailTask.dueDate}
                            onChange={(e) => {
                                const newDate = {
                                    ...detailTask,
                                    dueDate: e.target.value,
                                };
                                setDetailTask(newDate);
                            }}
                        />
                        <TextField
                            margin="dense"
                            label="Memo"
                            type="text"
                            fullWidth
                            variant="standard"
                            value={detailTask.content}
                            onChange={(e) => {
                                const newMemo = {
                                    ...detailTask,
                                    content: e.target.value,
                                };
                                setDetailTask(newMemo);
                            }}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={showModifyClose}>Cancel</Button>
                        <Button onClick={applyModify}>Modify</Button>
                    </DialogActions>
                </Dialog>
            </Grid>
        </Container>
    );
}

export default TodoList;
