import './LoginMain.css'
import {useUserStore} from "./useUserStore";
import MyAppBar from "../MyAppBar";
import {Button, Container} from "@mui/material";
import {useNavigate} from "react-router-dom";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import {useState} from 'react';
import axios from "axios";
import {REQUEST_URL} from "../../config/config";

export default function SignUpForm() {
    const {
        joinDTO,
        onJoinNameChange,
        onJoinIDChange,
        onJoinPWChange,
        RegisterSubmit
    } = useUserStore();
    const navigation = useNavigate();
    const [confirmPassword, setConfirmPassword] = useState('');

    const handleConfirmPasswordChange = (event) => {
        setConfirmPassword(event.target.value);
    };

    const handleRegisterSubmit = (event) => {
        event.preventDefault();
        if (joinDTO.pw === confirmPassword) {
            RegisterSubmit(joinDTO).then(() => {
                navigation("/auth");
            }).catch((error) => {
                alert(error.response.data.responseMessage);
            });
        } else {
            alert('비밀번호가 일치하지 않습니다.');
        }
    };
    const requsetDupCheck = async (id) => {
        return await axios.get(`${REQUEST_URL.DUPCHECK}`, {
            params: {
                userId: id
            }
        })
    }
    const handleCheckID = (event) => {
        event.preventDefault();
        requsetDupCheck(joinDTO.id).then((result) => {
            console.log(result.data)
            if (result.data) {
                alert('이미 사용중인 아이디입니다.');
            } else {
                alert('사용가능한 아이디입니다.');
            }
        }).catch((error) => {
            alert(error.response.data.responseMessage);
        });
    }

    return (
        <Container maxWidth="xs" disableGutters>
            <MyAppBar name={"Authentication"}/>
            <div className="main">
                <form className="login-form" onSubmit={handleRegisterSubmit}>
                    <div className="accountImg"><AccountCircleIcon sx={{fontSize: {xs: '125px'}}}/></div>
                    <label htmlFor="chk" aria-hidden="true">Register</label>
                    <input className="login-input" type="text" name="name" value={joinDTO.name}
                           onChange={onJoinNameChange}
                           placeholder="Name" required/>
                    <span style={{display: 'flex'}}>
                    <input className="login-input" type="text" name="id" value={joinDTO.id}
                           onChange={onJoinIDChange}
                           placeholder="ID" required/>
                  <Button
                      onClick={handleCheckID}
                      sx={{
                          position: "absolute",
                          marginTop: '10px',
                          right: "25px", /* 조정하여 input 내부에 위치하게 함 */
                          height: "40px", /* input 높이에 맞춤 */
                          background: "#c5e5fa",
                          color: "#0d0c22"
                      }}>확인</Button>
                        </span>
                    <input className="login-input" type="password" name="pw" value={joinDTO.pw}
                           onChange={onJoinPWChange}
                           placeholder="Password" required/>
                    <input className="login-input" type="password" name="confirmPw" value={confirmPassword}
                           onChange={handleConfirmPasswordChange}
                           placeholder="Confirm Password" required/>
                    <span style={{
                        float: 'left',
                        marginTop: '-.5rem',
                        fontSize: '.8125rem',
                        color: `${joinDTO.pw === confirmPassword ? 'green' : 'red'}`,
                    }}>{joinDTO.pw === '' || confirmPassword === '' ? null :
                        joinDTO.pw === confirmPassword ? '비밀번호가 일치합니다.' : '비밀번호가 일치하지 않습니다.'}
                    </span>
                    <input className="button" type="submit" value="Register"></input>


                </form>
            </div>
        </Container>
    )
}
