import {Map, MapMarker, MapTypeControl, ZoomControl} from "react-kakao-maps-sdk";
import {useEffect} from "react";
import {useDomesticStore} from "./useDomesticStore";
import MyMarker from "./MyMarker";

export default function KakaoMap() {

    const {center, categories} = useDomesticStore();

    useEffect(() => {

    }, [categories])
    return (
        <div style={{marginLeft: '2rem', marginRight: '2rem', marginBottom: '10vh'}}>
            <Map
                center={center}
                style={{zIndex: '0', borderRadius: '1rem', width: "100%", height: "60vh"}}
                level={5}
            >

                <MapMarker position={center}/>
                {
                    categories?.map((category) => (
                        category?.markers?.map((marker, index) => (
                                category.isVisible ? <MyMarker
                                    key={index}
                                    item={marker}
                                /> : null
                            )
                        )
                    ))
                }
                <MapTypeControl position={"TOPRIGHT"}/>
                <ZoomControl position={"RIGHT"}/>
            </Map>
        </div>
    )
}
