import "./App.css";
import ForeignMain from "./View/Foreign/ForeignMain";
import DomesticMain from "./View/Domestic/DomesticMain";
import {Route, Routes} from "react-router-dom";
import UnderBar from "./View/Underbar";
import {Container} from "@mui/material";
import SignUpForm from "./View/Authentication/SignUpForm";
import SignInForm from "./View/Authentication/SignInForm";
import axios from "axios";
import {useEffect} from "react";
import {useDomesticStore} from "./View/Domestic/useDomesticStore";
import TodoList from "./View/Todo/TodoList";
import {useCurrencyStore} from "./View/Foreign/useCurrencyStore";
import {REQUEST_URL, serviceKey, transformResponse} from "./config/config";


function App() {

    const {
        setCenter,
        dataList,
        setDataList,
        categories,
        setCategories,
    } = useDomesticStore();
    const {getCurrency} = useCurrencyStore();

    const request = async () => {
        await new Promise((resolve, reject) => {
            navigator.geolocation.getCurrentPosition(resolve, reject);
        })
            .then((position) => {
                setCenter({
                    lat: position.coords.latitude,
                    lng: position.coords.longitude,
                });
                return axios.get(REQUEST_URL.DOMESTICAPI, {
                    params: {
                        serviceKey: serviceKey,
                        numOfRows: 10000,
                        pageNo: 1,
                        _type: "json",
                        MobileOS: "ETC",
                        MobileApp: "TravelSupporter",
                        listYN: "Y",
                        arrange: "R",
                        mapX: position.coords.longitude,
                        mapY: position.coords.latitude,
                        radius: 10000,
                    },
                });
            })
            .then((response) => {
                const list = transformResponse(response.data.response);
                setDataList(list);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    useEffect(() => {
        let update = categories.map((category) => {
            return {
                ...category,
                isVisible: false,
                markers: dataList[category.listName] || [],
            };
        });
        setCategories(update);
    }, [dataList]);
    useEffect(() => {
    }, [categories]);

    useEffect(() => {
        request();
        getCurrency();
    }, []);

    return (
        <Container
            maxWidth={"xs"}
            style={{textAlign: "center", height: "100vh"}}
            disableGutters
        >
            <Routes>
                <Route path="/" element={<TodoList/>}/>
                <Route path="/foreign" element={<ForeignMain/>}/>
                <Route path="/domestic" element={<DomesticMain/>}/>
                <Route path="/auth" element={<SignInForm/>}/>
                <Route path="/signUp" element={<SignUpForm/>}/>
            </Routes>
            <Container
                maxWidth={"xs"}
                disableGutters
                style={{
                    position: "relative",
                    bottom: 0,
                    width: "100%",
                    padding: 0,
                    margin: 0,
                }}
            >
                <UnderBar/>
            </Container>
        </Container>
    );
}

export default App;
