import {create} from "zustand";
import axios from "axios";
import {REQUEST_URL} from "../../config/config";

export const useCurrencyStore = create((set, get) => ({
    currency: "JPY",
    setCurrency: (currency) => set({currency}),
    calcPrice: 0,
    setCalcPrice: (calcPrice) => set({calcPrice}),
    itemPrice: 0,
    setItemPrice: (itemPrice) => set({itemPrice}),
    itemList: [],
    setItemList: (itemList) => set({itemList}),
    currencyList: [
        {
            dealBasR: 0,
            currencyUnit: "AED",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "AUD",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "BHD",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "BND",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "CAD",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "CHF",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "CNH",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "DKK",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "EUR",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "GBP",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "HKD",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "IDR",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "JPY",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "KWD",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "MYR",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "NOK",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "NZD",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "SAR",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "SEK",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "SGD",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "THB",
            registeredDate: "2024-05-10"
        },
        {
            dealBasR: 0,
            currencyUnit: "USD",
            registeredDate: "2024-05-10"
        }
    ],
    setCurrencyList: (currencyList) => set({currencyList}),
    onSearchClick: async (item) => {
        await axios.get(REQUEST_URL.SEARCH, {
            params: {
                itemName: item
            }
        }).then((res) => {
            set(state => ({...state, itemList: res.data}))

        }).catch(e => {
            console.log(e);
        })
    },

    onPriceChange: (e) => {
        set((state) => {
            let price = e.target.value
            if (price === '' || price.match(/^[0-9]*\.?[0-9]*$/)) {
                let rate = state.currencyList.find((item) => item.currencyUnit === state.currency).dealBasR;
                rate = state.currency === 'JPY' || state.currency === 'IDR' ? rate / 100 : rate;
                const calcPrice = Math.round(price * rate);
                return {...state, itemPrice: price, calcPrice: calcPrice};
            }
            return state;
        });
    },
    selectOnChange: (e) => {
        set((state) => {
            const currency = e.target.value;
            let rate = state.currencyList.find((item) => item.currencyUnit === currency).dealBasR;
            rate = (currency === "JPY" || currency === "IDR") ? rate / 100 : rate;
            const calcPrice = Math.round(state.itemPrice * rate);
            return {...state, currency: currency, calcPrice: calcPrice};
        })
    },
    getCurrency: async () => {
        await axios.get(REQUEST_URL.CURRENCY)
            .then((res) => {
                set((state) => {
                    return {...state, currencyList: res.data};
                })
            })
            .catch(e => {
                console.log(e);
            })
    }
}));

