import {create} from "zustand";
import {persist} from "zustand/middleware";
import axios from "axios";
import {REQUEST_URL} from "../../config/config";

export const useUserStore = create(
    persist(
        (set, get) => ({
            joinDTO: {
                name: "",
                id: "",
                pw: "",
            },

            setJoinName: (newName) =>
                set(({joinDTO}) => ({joinDTO: {...joinDTO, name: newName}})),
            setJoinID: (newId) =>
                set(({joinDTO}) => ({joinDTO: {...joinDTO, id: newId}})),
            setJoinPW: (newPW) =>
                set(({joinDTO}) => ({joinDTO: {...joinDTO, pw: newPW}})),

            userDTO: {},
            setUserDTO: (userDTO) => set({userDTO}),

            loginDTO: {
                id: "",
                pw: "",
            },

            setLoginID: (newId) =>
                set(({loginDTO}) => ({loginDTO: {...loginDTO, id: newId}})),
            setLoginPW: (newPW) =>
                set(({loginDTO}) => ({loginDTO: {...loginDTO, pw: newPW}})),

            todoList: [],
            setTodoList: (todoList) => set({todoList}),

            isAuthenticated: false,
            setIsAuthenticated: (isAuthenticated) => set({isAuthenticated}),

            onIDChange: (e) => {
                const idValue = e.target.value;
                get().setLoginID(idValue);
            },

            onPWChange: (e) => {
                const pwValue = e.target.value;
                get().setLoginPW(pwValue);
            },

            handleLoginSubmit: async (e) => {
                e.preventDefault();
                await get().LoginSubmit(get().userDTO);
            },

            LoginSubmit: async () => {
                await axios
                    .get(REQUEST_URL.LOGIN, {
                        params: {
                            id: get().loginDTO.id,
                            pw: get().loginDTO.pw,
                        },
                    })
                    .then((res) => {
                        get().setUserDTO({id: res.data.id, name: res.data.name});
                        get().setTodoList(res.data.todoList);
                        get().setIsAuthenticated(true);
                        return res
                    })
            },

            onJoinNameChange: (e) => {
                const nameValue = e.target.value;
                get().setJoinName(nameValue);
            },

            onJoinIDChange: (e) => {
                const idValue = e.target.value;
                get().setJoinID(idValue);
            },

            onJoinPWChange: (e) => {
                const pwValue = e.target.value;
                get().setJoinPW(pwValue);
            },

            handleRegisterSubmit: async (e) => {
                e.preventDefault();
                await get().RegisterSubmit();
            },

            RegisterSubmit: async () => {
                return await axios.post(REQUEST_URL.JOIN, {
                    name: get().joinDTO.name,
                    id: get().joinDTO.id,
                    pw: get().joinDTO.pw,
                })
                    .then((res) => {
                        get().setUserDTO(res.data);
                        alert(res.data.name + "님 회원가입이 완료되었습니다.");
                        return res
                    })
            },

            logout: () => {
                sessionStorage.clear()
                window.location.reload()
            },
        }),
        {
            name: "persistStore",
            getStorage: () => sessionStorage,
        }
    )
);
